body,
html {
  height: 100%;
  font-family: "Fira Sans", sans-serif;
}

.hide {
  position: absolute !important;
  z-index: -999 !important;
  opacity: 0 !important;
  visibility: hidden !important;
}

.chat {
  display: flex;
  height: 100%;
  opacity: 0;
  transition: opacity .3s;
}

.chat-panel {
  height: 100%;
  background: #ff8663;
  /* Old browsers */
  background: linear-gradient(to right, #ff8663 0%, #ff8663 70%, #b25d45 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  width: 350px;
}

.chat-panel .user-profile {
  padding: 20px;
  display: flex;
  align-items: center;
}

.chat-panel .user-profile__avatar {
  width: 75px;
  height: 75px;
  overflow: hidden;
  border-radius: 150px;
  position: relative;
  cursor: pointer;
}

.chat-panel .user-profile__avatar img {
  width: 100%;
  height: 100%;
}

.chat-panel .user-profile__avatar:hover .user-profile__avatar-overlay {
  opacity: 1;
}

.chat-panel .user-profile__avatar-overlay {
  pointer-events: none;
  opacity: 0;
  line-height: 75px;
  text-transform: uppercase;
  font-size: 18px;
  padding: 0;
  margin: 0;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: white;
  background-color: rgba(0, 0, 0, 0.71);
  transition: opacity .3s;
}

.chat-panel .user-profile__name {
  flex: 1;
  margin-left: 20px;
  text-transform: capitalize;
  color: white;
}

.chat-panel .users {
  color: white;
  padding: 10px;
}

.chat-panel .users__title {
  margin: 0;
}

.chat-panel .users__count {
  margin-left: 5px;
}

.chat-panel .users__list {
  list-style: none;
  margin: 0;
  padding-top: 10px;
  padding-left: 20px;
}

.chat-panel .users .users-list__elem {
  margin-bottom: 6px;
}

.chat-area {
  background-color: #fff;
  flex: 1;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.message-list {
  list-style: none;
  padding: 0;
  flex: 1;
  overflow-y: auto;
}

.message-list__elem {
  background-color: whitesmoke;
  padding: 20px;
  border-radius: 30px 30px 30px 30px;
  margin-bottom: 15px;
  margin-right: 5px;
}

.message-list__elem:last-child {
  margin-bottom: 0;
}

.message-elem {
  display: flex;
  align-items: center;
}

.message-elem__avatar {
  overflow: hidden;
  width: 75px;
  height: 75px;
  border-radius: 150px;
}

.message-elem__avatar img {
  width: 100%;
  height: 100%;
}

.message-elem__content {
  margin-left: 15px;
  overflow-x: hidden;
  flex: 1;
}

.message-elem__name {
  font-weight: bold;
  text-transform: capitalize;
}

.message-elem__time {
  color: gray;
  margin-left: 15px;
}

.message-elem__message {
  word-break: break-word;
  overflow: hidden;
  word-wrap: break-word;
  text-overflow: ellipsis;
  overflow-wrap: break-word;
}

.message-form {
  margin-top: auto;
  width: 100%;
  display: flex;
}

.message-form__input {
  flex: 1;
  margin-right: 15px;
  border: 2px solid #f0f0f0;
  border-radius: 30px;
  max-height: 150px;
  padding-left: 15px;
  transition: box-shadow .3s;
}

.message-form__input:focus {
  outline: none;
  box-shadow: 0 0 15px -7px;
}

.message-form__submit {
  width: 150px;
  background-color: #ff8663;
  color: white;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  padding-top: 10px;
  padding-bottom: 10px;
}

.message-form__submit:hover {
  opacity: 0.9;
}

.modal-window {
  padding-top: 15px;
  padding-left: 7px;
  padding-right: 7px;
  padding-bottom: 7px;
  background-color: #f0f0f0;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 10px;
  box-shadow: 0 0 6px -3px;
}

.modal-window p {
  margin: 0;
}

.modal-window__header {
  padding-left: 15px;
}

.modal-window__body {
  background-color: white;
  padding: 15px;
  border-radius: 10px;
  margin-top: 10px;
}

.modal-wrapper::before {
  position: absolute;
  top: 0;
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.73);
}

.authentication {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.authentication__input {
  border: 1px solid #f0f0f0;
  border-radius: 50px;
  width: 350px;
  margin-bottom: 20px;
  padding-top: 10px;
  padding-left: 15px;
  padding-bottom: 10px;
  transition: box-shadow .3s;
}

.authentication__input:focus {
  outline: none;
  box-shadow: 0 0 15px -7px;
}

.authentication__submit {
  margin-left: auto;
}

.btn-default {
  width: 120px;
  background-color: #ff8663;
  color: white;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  margin-top: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.btn-default:focus {
  outline: none;
}

.btn-default:hover {
  opacity: 0.9;
}

.drag-n-drop {
  padding-top: 10px;
}

.drag-n-drop__preview {
  width: 100%;
  height: 100%;
}

.drag-n-drop__drop-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  text-transform: uppercase;
  color: gray;
  size: 18px;
  font-weight: bold;
  opacity: 0;
}

.drag-n-drop__zone {
  position: relative;
  border-radius: 15px;
  height: 250px;
  width: 250px;
  border: 3px dotted #f0f0f0;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  overflow: hidden;
  box-sizing: border-box;
}

.drag-n-drop__add {
  font-family: Arial, sans-serif;
  opacity: 0.7;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  border: 3px solid #cdcdcd;
  color: gray;
  border-radius: 10px;
  padding: 5px;
  background-color: #e6e6e6;
  cursor: pointer;
  transition: background-color .3s, opacity .3s;
  outline: none;
}

.drag-n-drop__add:focus {
  outline: none;
}

.drag-n-drop__add:hover {
  background-color: #fff;
  opacity: 1;
}

.drag-n-drop__btn-group {
  display: flex;
  justify-content: space-between;
}

.fade-in {
  animation: fadeIn 1s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.scale-inf {
  animation: scaleInf 2s infinite;
}

@keyframes scaleInf {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.5);
  }

  100% {
    transform: scale(1);
  }
}

.highlight {
  background-color: whitesmoke;
}

.prepare > * {
  visibility: hidden;
  opacity: 0;
}

.prepare > *:first-child {
  visibility: visible;
  opacity: 1;
}